<template>
  <div class="home">
    <div class="flex w300">
      <input
        class="wHalf margin5V"
        type="text"
        placeholder="Party ID"
        v-model="PID"
        @keyup.enter="partyJoin"
      >
      <a @click="partyJoin" class="wHalf margin5V">Rejoindre une partie</a>
      <a @click="partyCreate" class="wFull margin5V">Créer une partie</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      PID: '',
    };
  },
  methods: {
    partyCreate() {
      this.socket.emit('createParty', (newParty) => {
        this.PID = newParty.id;
        this.partyJoin();
      });
    },

    partyJoin() {
      if (this.PID.length > 4) this.$router.push(`/${this.PID}`);
    },
  },
};
</script>

<style scoped>
.home {
  position: absolute;
  top: calc(50% - 80px);
  left: calc(50% - 150px);
}
</style>
