<template>
  <div class="party">

    <div class="sidebar" v-if="
      party &&
      party.players &&
      party.players.length > 0 &&
      party.joinable
    ">
      <div v-for="(player, i) in party.players" :key="player">
        <svg viewBox="0 0 95 95" :style="{
          fill: (party.turn == i ? 'var(--color1)' : 'var(--text)')
        }">
          <g>
            <circle cx="47.5" cy="30.379" r="21.678"/>
            <path d="M62.475,49.976c-4.156,3.184-9.348,5.081-14.975,
            5.081s-10.818-1.897-14.975-5.081c-11.191,4.166-18.893,
            12.85-18.893,22.893c0,14.095,15.164,13.426,33.867,
            13.426c18.705,0,33.867,0.669,33.867-13.426C81.367,
            62.825,73.666,54.142,62.475,49.976z"/>
          </g>
        </svg>
        <div>{{ player }}</div>
      </div>
    </div>

    <div class="centerForm flex" v-if="step == 'name'">
      <input
        class="text wHalf"
        type="text"
        placeholder="Nom"
        v-model="newName"
        @keyup.enter="setName"
      >
      <a class="wHalf" @click="setName">Rejoindre</a>
    </div>

    <div class="absoluteCenter" v-if="waitingText != ''">
      {{ waitingText }}
    </div>

    <div class="absoluteCenter" v-if="
      party && party.players &&
      party.turn && party.started &&
      party.players[party.turn] != ''
    ">
      C'est le tour de {{ party.players[party.turn] }}
    </div>

    <h1 class="absoluteCenter wFull" v-if="
      playerWord != '' &&
      party.players[party.turn] != newName
    ">
      {{ playerWord }}
    </h1>

    <div class="absoluteCenter wFull" v-if="
      playerWord != '' &&
      party.players[party.turn] == newName
    ">
      <a @click="nextTurn()">Joueur suivant</a>
    </div>

    <div class="centerForm flex w75pr" v-if="
      party &&
      party.waitingForWord &&
      party.players[party.turn] == newName &&
      playerWord == ''
    ">
      <input
        class="wFull margin5V"
        type="text"
        placeholder="Mot"
        v-model="word"
      >
      <input
        class="wFull"
        type="text"
        placeholder="Exception"
        v-model="exceptionWord"
      >
      <a class="wFull margin15V" @click="sendWord">Envoyer</a>
    </div>

    <div
      class="foot hoverable"
      @click="startGame()"
      v-if="party && !party.started && party.owner == newName"
    >{{ (party.players.length > 2 ? "Jouer" : "En attente...") }}</div>

    <loading v-if="loading"></loading>
  </div>
</template>

<script>
import loading from '@/components/loading.vue';

export default {
  components: { loading },

  methods: {

    setName() {
      if (this.newName && this.newName.length >= 3) {
        localStorage.setItem('name', this.newName);
        this.step = '';
        this.socket.emit('joinParty', { username: this.newName, party: this.$route.params.party }, (rs) => {
          if (!rs.error) {
            this.party = rs.party;
            this.step = 'waiting';

            this.socket.on('partyUpdate', (party) => {
              this.party = party;
            });
          } else {
            this.$router.push('/');
          }
        });
      }
    },

    startGame() {
      this.socket.emit('partyStart');
    },

    sendWord() {
      this.socket.emit('word', this.word, this.exceptionWord);
      this.playerWord = '';
    },

    nextTurn() {
      this.socket.emit('nextTurn');
    },

  },

  mounted() {
    this.newName = localStorage.getItem('name');

    this.socket.on('word', (word) => {
      this.playerWord = '3...';
      setTimeout(() => {
        this.playerWord = '2...';
        setTimeout(() => {
          this.playerWord = '1...';
          setTimeout(() => {
            this.playerWord = word;
          }, 1000);
        }, 1000);
      }, 1000);
    });
  },

  data() {
    return {
      newName: '',
      party: null,
      loading: false,

      waitingText: '',
      word: '',
      exceptionWord: '',

      playerWord: '',

      step: 'name',

    };
  },

};


</script>

<style scoped>
.centerForm {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  margin: 0 auto;
}

.absoluteCenter {
  position: absolute;
  top: calc(50% - 30px);
  left: 0;
  margin: 0;
}

.sidebar {
  position: absolute;
  width: 100vw;
}

.sidebar > div {
  display: flex;
  background-color: var(--background-2);
  padding: 10px 0;
  height: 80px;
  font-size: 21px;
  line-height: 60px;
  text-transform: capitalize;
}

.sidebar > div > * {
  height: 100%;
  padding-left: 20px;
}

.foot {
  position: absolute;
  bottom: 0;
  background-color: var(--color1);
  line-height: 60px;
  width: 100vw;
  font-size: 21px;
  cursor: pointer;
}

.foot:hover { opacity: 0.8 }

</style>
